import { Component, OnInit, Input } from '@angular/core';
import { HomePageServicesService } from 'src/app/services/uiServices/home-page-services.service';

@Component({
  selector: 'app-social-media-icons',
  templateUrl: './social-media-icons.component.html',
  styleUrls: ['./social-media-icons.component.css']
})
export class SocialMediaIconsComponent implements OnInit {
  links: Array<Link> = [];
  @Input() type: string;
  constructor(private homeService: HomePageServicesService) { }

  ngOnInit() {
    console.log("Welcome in social media component");
    console.log(this.type);
    this.getLinks();

  }
  async getLinks() {
    this.links = await this.homeService.getLinksList();
  }
  onNavigate(url: string) {
    window.open(url.startsWith("http") ? url : "https://" + url, "_blank");
  }

}
